@import url(https://fonts.googleapis.com/css?family=Lato|Roboto:300,400,700&display=swap" rel="stylesheet);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body{margin:0;font-family:"Roboto", "Lato", sans-serif;font-size:16px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1{font-family:"Lato", sans-serif;font-size:3rem;font-weight:400;margin-bottom:0}@media screen and (max-width: 550px){h1{font-size:2rem}}h2{font-family:"Lato", sans-serif;font-size:1.5rem;font-weight:300;margin:1rem auto}@media screen and (max-width: 550px){h2{font-size:1rem;margin:0.5rem auto}}h3{font-family:"Roboto", sans-serif;font-size:1rem;font-weight:400;margin:0 auto;text-transform:uppercase}

.Leaderboard__score-list{font-family:"Roboto", sans-serif;font-size:1rem;font-weight:300;margin:.5rem auto;width:15rem;border-bottom:1px solid #c5c5c5;border-top:1px solid #c5c5c5}.Leaderboard__score-row{border-top:1px solid #c5c5c5;padding:.5rem}.Leaderboard__place{float:left;max-width:30%}.Leaderboard__name{margin-left:3rem;max-width:40%;text-transform:capitalize}.Leaderboard__score{float:right;margin-left:3rem;max-width:30%}

.App{text-align:center}@media screen and (max-width: 800px){.App{font-size:14px}}.App button{background-color:#316bd3;border:2px solid #316bd3;border-radius:0.5rem;box-shadow:none;color:#FFFFFF;cursor:pointer;font-family:"Lato", sans-serif;font-size:1rem;padding:1rem 3rem;margin:2rem;text-transform:uppercase}.App button:hover{background-color:#FFFFFF;border:2px solid #316bd3;color:#316bd3}.App__difficulty-form{border:1px solid #c5c5c5;border-radius:0.5rem;display:flex;justify-content:space-between;list-style:none;margin:2rem auto 0;max-width:30%;padding:0}@media screen and (max-width: 1200px){.App__difficulty-form{max-width:40%}}@media screen and (max-width: 950px){.App__difficulty-form{max-width:50%}}@media screen and (max-width: 640px){.App__difficulty-form{max-width:75%}}@media screen and (max-width: 550px){.App__difficulty-form{max-width:80%}}.App__difficulty-form__container{display:inline-block;width:calc(100%/3)}.App__difficulty-form__container input[type="radio"]:checked+label{border:1px solid #316bd3;border-radius:0.5rem;color:#316bd3;font-weight:700}.App__difficulty-form__container label{box-sizing:border-box;cursor:pointer;display:block;padding:1rem 2rem;width:100%}@media screen and (max-width: 550px){.App__difficulty-form__container label{padding:1rem 1rem}}.App__difficulty-form__container label:hover{border:1px dashed #31add3;border-radius:0.5rem;color:#31add3;font-weight:500}input[type="radio"]{display:none}

.Card{font-size:16px;margin:0.6rem;width:calc(100%/5 - 1.25rem)}.Card__box{display:inline-block;position:relative}.Card__image{width:100%;border-radius:0.3125rem;box-shadow:0 0.125rem 0.1875rem 0.125rem #ccc;cursor:pointer}.Card__box .Card__name{color:#FFFFFF;font-size:1.5rem;position:absolute;margin:0 auto;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);top:70%;z-index:999}@media screen and (max-width: 800px){.Card{font-size:12px;width:calc(100%/4 - 1.25rem)}.Card__box .Card__name{color:#FFFFFF;font-size:1rem}}@media screen and (max-width: 480px){.Card{font-size:8px;width:calc(100%/3 - 1.25rem)}.Card__box .Card__name{color:#FFFFFF;font-size:1rem}}

.GameEnd__high-score{color:#316bd3;font-family:"Lato", sans-serif;font-size:2rem;font-weight:700;margin:1rem auto}@media screen and (max-width: 480px){.GameEnd__high-score{margin:0 auto 1rem}}.GameEnd__final-score-container{display:inline-block;border:3px solid #000000;border-radius:50%;min-width:20px;min-height:20px;padding:2rem;color:white;text-align:center;line-height:1;box-sizing:content-box;margin:1rem;white-space:nowrap}.GameEnd__final-score-container__final-score{color:#000000;font-family:"Lato", sans-serif;font-size:3rem;display:inline-block;vertical-align:middle}.GameEnd__final-score-container:before{content:"";display:inline-block;vertical-align:middle;padding-top:100%;height:0}.GameEnd__score-caption{font-family:"Lato", sans-serif;font-size:1.5rem;font-weight:300;margin:2rem auto 0rem}@media screen and (max-width: 480px){.GameEnd__score-caption{margin:1rem auto -0.5rem}}.GameEnd__save-high-score text,.GameEnd__save-high-score label{display:block;margin-bottom:-0.5rem;margin-left:1rem;text-align:left}@media screen and (max-width: 480px){.GameEnd__save-high-score text,.GameEnd__save-high-score label{margin-left:0rem}}.GameEnd__save-high-score__initial-container{display:inline-block}#initial-input{align-items:center;background-color:#FFFFFF;border:2px solid #316bd3;border-radius:0.5rem;box-shadow:none;color:#316bd3;font-family:"Lato", sans-serif;font-size:1rem;padding:1rem 1rem;margin:1rem}@media screen and (max-width: 480px){#initial-input{margin:1rem auto -0.5rem}}#initial-input:focus{border:2px solid #316bd3;border-radius:0.5rem;box-shadow:0.5px 0.5px 1px;color:#316bd3;outline:none}#play-again{background-color:#FFFFFF;border:2px solid #316bd3;color:#316bd3}#play-again:hover{background-color:#316bd3;border:2px solid #316bd3;color:#FFFFFF}

.Game{text-align:center}@media screen and (max-width: 480px){.Game{margin-top:1rem}}.Game__card-container{display:flex;flex-direction:row;flex-wrap:wrap;margin:0 15%}@media screen and (max-width: 800px){.Game__card-container{margin:0 10%}}@media screen and (max-width: 480px){.Game__card-container{margin:0 5%}}.Game__score-display{font-family:"Lato", sans-serif;font-size:2rem;margin:1.5rem auto 0}@media screen and (max-width: 480px){.Game__score-display{font-size:1.7rem;margin:0 auto}}@media screen and (max-width: 480px){#reset-button{padding:0.7rem 2rem}}

